<template>
  <div>
    <h3>模板语法</h3>
    <span>Message: {{ msg }}</span>
    <span v-html="rawHtml"></span>
    <code><span v-html="rawHtml"></span></code>
    <span>属性attribute绑定用v-bind</span>
    <span :style="style" :[title]="titleValue" @click="setAttr">颜色</span>
    <input v-model.trim="msg">
    <span>age:{{age}}</span>
    <input v-model.number="age" type="number">
    <div>v-model.lazy会在change后才改变值</div>
    <input v-model.lazy="msg">
    <div @click="ifelse=!ifelse">条件渲染，点击我会消失</div>
    <template v-if="ifelse" >
      <span >满足条件才显示 </span>
    </template>
    <div>v-for 对象</div>
    <div v-for="(value, name) in person" :key="name">
    {{ name }}: {{ value }}
    </div>

    <ul id="example-1">
  <li v-for="item in items" :key="item.message">
    {{ item.message }}
  </li>
  <hr>
  <ul v-for="a in testArray" :key="a">
    <li>{{a}}</li>
  </ul>
  <div>数组排序<el-button @click="arrSort">排序</el-button></div>
</ul>
  <h3>侦听属性</h3>
  <div id="demo">{{ fullNames }}</div>
  <h3>事件处理</h3>
  <el-button @click="age++">点击测试+</el-button>
    <el-button v-on:click="age--">点击测试-</el-button>
  <span>{{age}}</span>
  <h3>绑定值</h3>
  <el-input v-model="msg"></el-input>
  <el-checkbox v-model="ifelse"></el-checkbox>
  <el-input type="textarea" v-model="tempName"></el-input>
  <div class="line"></div>
  <h3>组件示例</h3>
  <template>
    <p>命名规范：字母全小写且必须包含一个连字符。也可以使用 PascalCase首字母大写方式</p>
    <p><el-tag type="danger">注意</el-tag>局部注册的组件在其子组件中不可用</p>
    <p>全局注册</p>
    <my-component></my-component>
     <pre>Vue.component('my-component-name', { /* ... */ })</pre>
     <span>组件复用效果</span>
     <my-component></my-component>
     <my-component></my-component>
     <my-component></my-component>
     <my-menu v-for="p in posts" :key="p.id" :title="p.title" :id="p.id"></my-menu>
     <h3>传递静态或动态 Prop,prop可指定类型String,Number,Boolean,Array,Object,Function,Promise</h3>
     <my-component title="这是我通过props传入的值"></my-component>
     <p><el-tag type="danger">注意</el-tag>每个组件必须只有一个根元素</p>
      <pre>{{tagHTML}}
      </pre>
 <h3>局部注册</h3>
  <p>局部注册可用于单个页面组件使用，避免全局无谓的加载js</p>
  <pre>
    var ComponentA = { /* ... */ }
    var ComponentB = { /* ... */ }
    var ComponentC = { /* ... */ }
    new Vue({
      el: '#app',
      components: {
        'component-a': ComponentA,
        'component-b': ComponentB
      }
    })
  </pre>
  </template>
  <my-button @click="test1">bbbbbbb</my-button>
  <my-button @click="test2">cccc</my-button>
  <my-component :title="title">
    <my-menu :title="title">cccc</my-menu>
  </my-component>
  <my-menu :title="title">cccc</my-menu>
  <el-button @click="title='xxxx'">向下</el-button>
  </div>
</template>
<script>
import myButton from '../components/my-button.vue'
export default {
  components: { myButton },
  name: 'testPage',
  data () {
    return {
      tagHTML: `<div>
    <h3>{{ title }}</h3>
    <div v-html="content"></div>
</div>`,
      toggle: true,
      fullName: 'fffffffff',
      msg: '插值',
      age: 22,
      rawHtml: '<h1>这是Html语言h1</h1><a href="www.baidu.com">百度</a>',
      style: { fontSize: '30px' },
      title: 'title',
      titleValue: 'testet',
      ifelse: true,
      person: { name: 'yangqi', age: 33 },
      items: [{ message: 'Foo' }, { message: 'Bar' }, { message: 'kood' }, { message: 'jbm' }],
      testArray: [12, 4, 34, 55, 65, 7],
      tempName: '这是你想要的吧一？',
      posts: [
        { id: 1, title: '菜单标题一' },
        { id: 2, title: '菜单标题二' },
        { id: 3, title: '菜单标题三' }
      ]
    }
  },
  watch: {
    title: function () {
      console.log('title发生变化')
    },
    // 说的不建议用这种方式，用计算属性
    fullName: function () {
      console.log('fullName发生改变')
    }
  },
  computed: {
    titles: function () {
      return this.title
    },
    fullNames: {
      get: function () {
        console.log('你调用了')
        return this.tempName
      },
      set: function (newValue) {
        console.log('你设置了fullNames')
        this.tempName = newValue
      }
    }
  },
  methods: {
    setAttr: function () {
      this.title = 'class'
      this.titleValue = 'bold'
    },
    arrSort: function () {
      return this.testArray.sort(function (a, b) { return a - b })
    },
    test1 () { alert('test1') },
    test2 () { alert('test2') }
  }
}
</script>

<style scoped>
.bold {
  font-weight: 900;
  color: aqua;
}
.line {
 border-bottom-color: #333;
 border-bottom-style: solid;
 border-bottom-width: 1px;
 margin-top:10px;
}
pre {
  font-size: 16px;
  font-family: 'lucida console';
}
</style>
